.checkoutProduct{
display: flex;
margin-top: 20px;
margin-bottom: 20px;
}

.checkoutProduct__image{
    object-fit: contain;
    width: 180px;
    height: 180px;
}

.checkoutProduct__info{
padding-left: 20px;
}

.checkoutProduct__info > button{
    margin-top: 10px;
    color: #111;
    background-color: #f0c14b;
    border: 1px solid ;
    border-color: #a88734 #9c7e31 #846a29 ;
}
.checkoutProduct__rating{
    display: flex;
}
.checkoutProduct__price{
    
}

.checkoutProduct__title{
    font-size: 17px;
    font-weight: 800;
}